body {
  background: #f8f8f8;
  color: #333333;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400;
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "league-gothic";
  font-weight: 400;
}

h1 {
  font-size: 36px;
  text-transform: uppercase;
}

.tile {
  position: relative;
  overflow: hidden;
  top: 0;
}
.tile img.project-thumb {
  display: block;
  width: 100%;
  transition: all 0.5s ease-in-out;
  transform: translateX(0%);
}
.tile img.project-thumb.project-thumb-active {
  transition: all 3s cubic-bezier(0.075, 0.82, 0.165, 1);
  transform: scale(1.07);
}
.tile .overlay {
  width: 100%;
  height: 100%;
  background: rgba(108, 172, 228, 0.45);
  opacity: 0;
  position: absolute;
  top: 0;
  display: block !important;
}
.tile .overlay.overlay-active, .tile .overlay.overlay-active-add {
  opacity: 1;
}
.tile a {
  text-decoration: none;
}
.tile span.project-name {
  width: 100%;
  height: 100%;
  padding: 20px;
  color: white;
  font-size: 28px;
  text-transform: uppercase;
  display: block;
  line-height: 1em;
  font-family: "alternate-gothic-no-2-d";
  font-weight: 400;
}

span.red {
  color: #800000;
}

hr {
  border-top: 1px solid #aaa;
}

.tile-wrapper {
  width: 50%;
  float: left;
}

#project {
  position: relative;
}

.project-img {
  margin-bottom: 30px;
}

.remove {
  position: absolute;
  right: 15px;
  top: 0;
  cursor: pointer;
}
.remove a {
  color: #333333;
}
.remove a:hover {
  color: #333333;
}

footer {
  height: 62px;
  width: 100%;
  background: #800;
  color: #ccc;
  padding: 10px 0;
  text-align: center;
}
footer ul.social {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
footer ul.social li {
  display: inline-block;
  margin-right: 10px;
}
footer ul.social li:last-child {
  margin-right: 0;
}
footer ul.social li a {
  color: #800000;
  transition: color .2s;
}
footer ul.social li a:hover {
  color: #333333;
}
footer ul.social li a:hover .fa-circle {
  color: #f8f8f8;
}

