/*==========  Mobile First Method  ==========*/
/* Custom, iPhone Retina */
/* Extra Small Devices, Phones */
@media only screen and (max-width: 768px) {
  .tile img.project-thumb {
    position: static;
    transition: none;
  }
  .tile img.project-thumb.project-thumb-active {
    scale: 1.0;
  }
  .tile .overlay {
    width: 100%;
    height: auto;
    position: absolute;
    bottom: 0;
    top: auto;
    display: none;
  }
  .tile .overlay span.project-name {
    padding: 5px;
  }
}
/* Small Devices, Tablets */
@media only screen and (min-width: 768px) {
  .tile-wrapper {
    width: 33.33333%;
  }
}
/* Medium Devices, Desktops */
@media only screen and (min-width: 992px) {
  .tile-wrapper {
    width: 33.33333%;
  }
}
/* Large Devices, Wide Screens */
@media only screen and (min-width: 1200px) {
  .tile-wrapper {
    width: 25%;
  }
}

